
import { useState } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { produce } from 'immer';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TabPanel, TextControl, ToggleControl, SelectControl, __experimentalBoxControl as BoxControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';

// Settings Components
import { Label, Background, BColor, BorderControl, ColorsControl, Typography, HelpPanel, BtnGroup, BDevice } from '../../Components';

import { tabController, checkTheme } from './utils/functions';
import options, { aligns } from './utils/options';
import { PanelRow } from '@wordpress/components';
import { perUnit } from '../../Components/utils/options';
const { generalStyleTabs, pxUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes, audienceOpt }) => {
	const { config, elements, content, message, placeholder, theme, icon, iconBG, titleTypo, titleColor, descTypo, descColor, formBG, formPadding, formBorder, fieldTypo, fieldColors, fieldPadding, fieldBorder, fieldMargin, btnTypo, btnColors, btnHovColors, btnPadding, btnBorder, messageColors, messageTypo, themeWidth, themeAlign } = attributes;
	const [themeWidthDevice, setThemeWidthDevice] = useState('desktop');

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel mcbTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>

					<HelpPanel slug="block-for-mailchimp" docsLink="https://bplugins.com/docs/mailchimp-block" />

					<PanelBody className='bPlPanelBody mcbPanelBody' title={__('Config', 'mail-collections')} initialOpen={false}>
						<div className='mcbConfigApiKey'>
							<button className='mcbBtn' onClick={() => {
								wp.data.dispatch('core/edit-post').openGeneralSidebar('mcb-mailchimp/mcb-mailchimp');
							}} >{__('Connection', 'mail-collections')}</button>
						</div>
						<SelectControl className='' label={__('Select Audience', 'mail-collections')} options={[{ label: 'Select', value: '' }, ...audienceOpt]} value={config?.audienceId} onChange={(val) => { setAttributes({ config: { ...config, audienceId: val } }); }} />
					</PanelBody>

					<PanelBody className="bPlPanelBody" title={__('Elements', 'mail-collections')} initialOpen={false}>

						{('form1' === theme || 'form2' === theme) &&
							<ToggleControl className='mt10' label={__('Icon', 'mail-collections')} checked={elements?.icon} onChange={(val) => { setAttributes({ elements: { ...elements, icon: val } }) }} />}

						<ToggleControl className='mt10' label={__('Title', 'mail-collections')} checked={elements?.title} onChange={(val) => { setAttributes({ elements: { ...elements, title: val } }) }} />

						<ToggleControl className='mt10' label={__('Description', 'mail-collections')} checked={elements?.desc} onChange={(val) => { setAttributes({ elements: { ...elements, desc: val } }) }} />

						{('default' === theme || 'form3' === theme) && <>
							<ToggleControl className='mt10' label={__('First Name', 'mail-collections')} checked={elements?.fName} onChange={(val) => { setAttributes({ elements: { ...elements, fName: val } }) }} />

							<ToggleControl className='mt10' label={__('Last Name', 'mail-collections')} checked={elements?.lName} onChange={(val) => { setAttributes({ elements: { ...elements, lName: val } }) }} />
						</>}

					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Button', 'mail-collections')} initialOpen={false}>
						<TextControl label='' value={content.button} onChange={(val) => setAttributes({ content: { ...content, button: val } })} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Message', 'mail-collections')} initialOpen={false}>
						<Label className='mt5'>{__('Success', 'mail-collections')}</Label>
						<TextControl label='' value={message.success} onChange={(val) => { setAttributes({ message: { ...message, success: val } }) }} />

						<Label className='mt5'>{__('Error', 'mail-collections')}</Label>
						<TextControl label='' value={message?.error} onChange={(val) => { setAttributes({ message: { ...message, error: val } }) }} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Placeholder Text', 'mail-collections')} initialOpen={false}>
						{('default' === theme || 'form3' === theme) && <>

							{elements?.fName && <>
								<Label className='mt5'>{__('First Name', 'mail-collections')}</Label>
								<TextControl label='' value={placeholder?.fName} onChange={(val) => { setAttributes({ placeholder: { ...placeholder, fName: val } }) }} /> </>}

							{elements?.lName && <>
								<Label className='mt5'>{__('Last Name', 'mail-collections')}</Label>
								<TextControl label='' value={placeholder?.lName} onChange={(val) => { setAttributes({ placeholder: { ...placeholder, lName: val } }) }} /></>
							}
						</>}
						<Label className='mt5'>{__('Email', 'mail-collections')}</Label>
						<TextControl value={placeholder?.email} onChange={(val) => { setAttributes({ placeholder: { ...placeholder, email: val } }) }} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Themes', 'mail-collections')} initialOpen={false}>
						<SelectControl label={__('Select Theme', 'mail-collections')} labelPosition="side" value={theme} options={options?.forms} onChange={(val) => {
							setAttributes({ theme: val, ...checkTheme(val) })
						}} />

						{/* Width define option  */}
						<PanelRow className='m0'>
							<Label mt='0'>{__('Width:', 'mail-collections')}</Label>
							<BDevice device={themeWidthDevice} onChange={val => setThemeWidthDevice(val)} />
						</PanelRow>

						<UnitControl value={themeWidth[themeWidthDevice]} onChange={val => { setAttributes({ themeWidth: { ...themeWidth, [themeWidthDevice]: val } }) }} units={[pxUnit(550), perUnit(100)]} isResetValueOnUnitChange={true} />

						<PanelRow className='m0'>
							<p></p>
							<BDevice device={themeWidthDevice} onChange={val => setThemeWidthDevice(val)} />
						</PanelRow>
						<PanelRow className='themeAlign m0'>
							<Label className="m0" mt='0'>{__('Align:', 'mail-collections')}</Label>
							<BtnGroup value={themeAlign[themeWidthDevice]} onChange={val => setAttributes({ themeAlign: { ...themeAlign, [themeWidthDevice]: val } })} options={aligns} isIcon={true} />
						</PanelRow>

					</PanelBody>
				</>}

				{'style' === tab.name && <>

					<PanelBody className='bPlPanelBody' title={__('Form', 'mail-collections')} initialOpen={false}>
						<Background className='mb10' label={__('Background:', 'mail-collections')} value={formBG} onChange={val => setAttributes({ formBG: val })} />

						<BoxControl label={__('Padding', 'mail-collections')} values={formPadding} onChange={val => setAttributes({ formPadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />

						<BorderControl className='' label={__('Border', 'mail-collections')} value={formBorder}
							onChange={(val) => { setAttributes({ formBorder: val }) }} />

					</PanelBody>

					{(elements?.icon && ('form1' === theme || 'form2' === theme)) &&
						<PanelBody className='bPlPanelBody' title={__('Icon', 'mail-collections')} initialOpen={false}>
							<Background className='mb10' label={__('Background', 'mail-collections')} value={iconBG} onChange={val => setAttributes({ iconBG: val })} />

							<BColor className='mb10' label={__('Color', 'mail-collections')} value={icon?.color} onChange={val => setAttributes({ icon: { ...icon, color: val } })} />
						</PanelBody>}

					{elements?.title &&
						<PanelBody className='bPlPanelBody' title={__('Label', 'mail-collections')} initialOpen={false}>
							<Typography className='mt10' label={__('Typography', 'mail-collections')} value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} produce={produce} />

							<BColor label={__('Color', 'github')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} />
						</PanelBody>}

					{elements?.desc &&
						<PanelBody className='bPlPanelBody' title={__('Description', 'mail-collections')} initialOpen={false}>
							<Typography className='mt10' label={__('Typography', 'mail-collections')} value={descTypo} onChange={val => setAttributes({ descTypo: val })} produce={produce} />

							<BColor label={__('Color', 'github')} value={descColor}
								onChange={val => setAttributes({ descColor: val })} />
						</PanelBody>}

					<PanelBody className='bPlPanelBody' title={__('Input', 'mail-collections')} initialOpen={false}>

						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={fieldTypo} onChange={val => setAttributes({ fieldTypo: val })} produce={produce} />

						<ColorsControl className='mb15' label={__('Colors', 'mail-collections')} value={fieldColors} onChange={val => setAttributes({ fieldColors: val })} defaults={{ color: '#fff', bg: '#000' }} />

						<BoxControl label={__('Padding', 'mail-collections')} values={fieldPadding} onChange={val => setAttributes({ fieldPadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />

						<BorderControl className='mb15' label={__('Border', 'mail-collections')} value={fieldBorder}
							onChange={(val) => { setAttributes({ fieldBorder: val }) }} />

						<BoxControl label={__('Margin', 'mail-collections')} values={fieldMargin} onChange={val => setAttributes({ fieldMargin: val })} resetValues={{ top: 0, right: 0, bottom: '10px', left: 0 }} units={[pxUnit(3), emUnit(2)]} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Button', 'mail-collections')} initialOpen={false}>
						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={btnTypo} onChange={val => setAttributes({ btnTypo: val })} produce={produce} />

						<ColorsControl className='' label={__('Colors', 'mail-collections')} value={btnColors} onChange={val => setAttributes({ btnColors: val })} />

						<ColorsControl className='mb10' label={__('Hovers Colors', 'mail-collections')} value={btnHovColors} onChange={val => setAttributes({ btnHovColors: val })} />

						<BoxControl label={__('Padding', 'mail-collections')} values={btnPadding} onChange={val => setAttributes({ btnPadding: val })} resetValues={{ top: 0, right: 0, bottom: 0, left: 0 }} units={[pxUnit(3), emUnit(2)]} />

						<BorderControl className='' label={__('Border', 'mail-collections')} value={btnBorder}
							onChange={(val) => { setAttributes({ btnBorder: val }) }} />
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Message', 'mail-collections')} initialOpen={false}>

						<Typography className='mt10' label={__('Typography', 'mail-collections')} value={messageTypo} onChange={val => setAttributes({ messageTypo: val })} produce={produce} />

						<BColor label={__('Success', 'mail-collections')} value={messageColors?.success} onChange={(val) => { setAttributes({ messageColors: { ...messageColors, success: val } }) }} />

						<BColor label={__('Error', 'mail-collections')} value={messageColors?.error} onChange={(val) => { setAttributes({ messageColors: { ...messageColors, error: val } }) }} />

					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>
	</>;
};
export default Settings;